@import url(https://db.onlinewebfonts.com/c/a082fd3df68a0b54e0d4d794bc38d268?family=Blender+Pro);
:root {
  --primary-color: #d0eb5c;
  --primary-color-dark: #62740a;

  --accent-color: #448a91;

  --success-color: #5ceb8c;

  --active-color: rgb(41, 44, 59);

  --disabled-color: #999999;
  --disabled-color-dark: rgb(65, 65, 65);

  --background-color: rgb(19, 15, 24);
}

@font-face {
  font-family: 'Blender Pro';
  src: url('https://db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.eot');
  src: url('https://db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.eot?#iefix')
      format('embedded-opentype'),
    url('https://db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.woff2')
      format('woff2'),
    url('https://db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.woff')
      format('woff'),
    url('https://db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.ttf')
      format('truetype'),
    url('https://db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.svg#Blender Pro')
      format('svg');
}

html {
  font-size: 62.5%;
}

* {
  box-sizing: border-box;
}

body {
  font-size: 1.6rem;
  font-family: 'Blender Pro', sans-serif;
  background-color: var(--background-color);
  color: var(--yellow);
  padding: 4%;
  height: 100vh;
  margin: 0;
}

#root {
  height: 100%;
}

